

$mainColor : #e9530e;

//애니메이션
.wow-enter {
    opacity: 0;
}

.wow-enter-active {
    opacity: 1;
    transition: all 500ms;
}

.tabDiv{
    margin: 2.5rem 0 2.5rem 0;
}

.red{
    color: #ec0808;
}

@mixin aa {
   color: blue;
} 

.product-alert{
    background-color: #f3d193;
    padding: 20px;
    text-align: center;
    max-width: 500px;
    margin: auto;
    border-radius: 8px;
    
    p{
        margin: 0;
    }
}

.product-alert2{
    @extend .product-alert;
    @include aa;
    background-color: #ec0808;
}

